<template>
    <div class="mvlist">
        <mt-header fixed title="电影订单">
           <router-link to="mine" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
                <mt-button icon="more" slot="right"></mt-button>
        </mt-header>
        <div v-if="lists.length===0" class="nogoods">
            <p style="position: relative; top:2.4rem;left:1.2rem;font-size:16px;width:150px;">这里什么都没有!!!!!!</p> 
            <router-link to="/home"><mt-button style="position: relative; top:2.6rem;left:1.4rem;font-size:16px;width:100px;" type="danger">去购买</mt-button></router-link>  
        </div>
        <div class="conpar" v-else>
            <div  
        
        class="con"
        v-for="list in lists"
        :key="list.filmId"
        >
            <div class="left">
                <span><img :src="list.poster" alt=""></span>
            </div>
            <div class="right">
                <p>{{list.name}}</p>
                <p>{{list.category}}&nbsp;{{list.language}}</p>
                <p>￥{{list.money}} *{{list.num}}</p>
            </div>
            <mt-button @click.stop="addmovies(list)"  style="position: absolute;right:60px;" type="danger">+</mt-button>
            <mt-button @click.stop="reducemovies(list)"  style="position:absolute;right:10px;width:35px;" type="danger">-</mt-button>
        </div>   
        <div class="count">
            <p>共{{countNum.num}}件商品</p>
            实付: <p>￥{{countNum.price}}</p>
        </div> 
        <div class="buy">
            <p @click="quxiao">订单取消</p>
            <mt-button @click="promise">去结算</mt-button>
        </div>
        </div>
    </div>
</template>  


<script> 
import { MessageBox } from 'mint-ui';
import {mapActions,mapState,mapGetters} from "vuex"
export default {
    computed:{
        ...mapState({
            lists:state=>state.myList.list
        }),
        ...mapGetters(["countNum"])
    },
    methods:{
        ...mapActions(["addmovies","reducemovies","removemovies"]),
        promise(){
           MessageBox.confirm('共计￥'+this.countNum.price+'确定要支付吗？').then(action => {
               this.removemovies()
     })
        },
        quxiao(){
             MessageBox.confirm('确定取消订单吗').then(action=> {
               this.removemovies()
            })
        }
    },
    data(){
        return {
            flag:true
        }
    },
    created(){
        // console.log(this.lists)
    }
    
}
</script>


<style scoped lang="scss">
.mint-header{
    height: 0.5rem;
    background: #fff;
    color:#000;
    font-size:16px;
}
.conpar{
    margin-top:50px;
}
.con{
    
    padding:15px ;
    height: 1rem;
    width: 3.75rem;
    .left{
        float:left;
        display: flex;
        align-items: center;
        width: 0.5rem;
        height: 0.7rem;
        img{
            width: 100%;
            height:0.33rem;
            
        }
    }
    .right{
        float:left;
        display: flex;
        flex-flow: column;
        justify-content: center;
        p{
            margin-left:15px;
        }
        p:nth-of-type(1){
            color:#000;
            font-size: 16px;
        }
         p:nth-of-type(2){
              margin-top:5px;
            color:#ccc;
            font-size: 12px;
        }
          p:nth-of-type(3){
            margin-top:5px;
            color:#ff5f16;
            font-size: 16px;
        }
    }
}
.count{
    height: 0.4rem;
    width: 3.75rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    p:nth-of-type(1){
        margin-right:10px;
    }
      p:nth-of-type(2){
          font-size:16px;
        margin-right:15px;
        color: #ff5f16;
    }
}
.buy{
        height: 0.5rem;
    width: 3.75rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;    
    .mint-button{
    color:#fff;
    font-weight: 900;
    background: red;
    }
}

</style>